@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo:ital,wght@1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,500;1,700;1,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont,  'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.2)),url(/personal_website/static/media/welcome_page.f59f010e.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center calc(100% - 25%);
  background-size: cover !important;
  -webkit-background-size: cover !important;
}

.svg-link {
  font-size: 1.5em !important;
}

.section-title {
  text-shadow:  0 0 15px rgba(255, 37, 37, 0.4);
  font-size: 1em;
  text-align: center;
  font-family: 'Montserrat', sans-serif !important;
  font-weight: 700;
  font-style: italic;
}

.sub-title {
  font-size: 1em;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
}

.description {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 0.6em;
}

.particle-bg {
  height: 100% !important;
}

.particle-div {
  position: absolute;
  width:100%;
  height:100%;
  top: 0;
}

.particle-behind {
  z-index: -1
}

.page-div {
  padding-top: 25%;
  padding-bottom: 7%;
  height: 100%;
}


.title-horizontal-right {
  height: 0.3vw;
  position: absolute; 
  width: calc(100% - 30px);
  top: 55%;
  background-color: black;
  border-bottom-right-radius: 75%;
}

.title-horizontal-left {
  height: 0.3vw;
  position: absolute; 
  width: calc(100% - 30px);
  top: 55%;
  background-color: black;
  border-bottom-left-radius: 75%;
}

.end-horizontal {
  height: 0.3vw;
  width: 100%;    
  background-color: black;
  border-top-left-radius: 50% 80%;
  border-top-right-radius: 50% 80%;
}

.end-row {
  margin-top: 5%;
}

.border-divider {
  border-bottom: 2px solid lightgray;
}

@media only screen and (min-width: 375px) {
  .page-div {
    padding-top: 15%;
  }

  .section-title {
    font-size: 28px;
  }

  .sub-title {
    font-size: 1.2em;
  }

  .description {
    font-size: 0.9em;
  }

  .svg-link {
    font-size: 2em !important;
  }
}

@media only screen and (min-width: 768px) {
  .page-div {
    padding-top: 10%;
  }

  .section-title {
    font-size: 35px;
  }

  .sub-title {
    font-size: 1.2em;
  }

  .description {
    font-size: 0.8em;
  }
}

@media only screen and (min-width: 992px) {
  .page-div {
    padding-top: 5%;
  }

  .section-title {
    font-size: 50px;
  }

  .sub-title {
    font-size: 1.6em;
  }

  .description {
    font-size: 1em;
  }
}
.navbar-color {
    background: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.7));;
}

.navbar-block {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    font-family: 'Montserrat', sans-serif;
}

.nav-link {
    transition-duration: 0.6s;
}

.nav-link:hover {
    transition-duration: 0.3s;
}

.nav-link > .navbar-block > .navbar-underline {
    height: 1px;
    background-color: transparent;
    transition-duration: 0.6s;
}

.nav-link:hover > .navbar-block > .navbar-underline {
    height: 1px;
    background-color:rgba(255, 0, 0, 0.6);
    transition-duration: 0.3s;
}

.active > .navbar-block > .navbar-underline {
    height: 1px;
    background-color:red !important;
}

@media only screen and (min-width: 768px) {
    .navbar-color {
        background: linear-gradient(to right,rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
    }
}
#welcome {
    height: 100vh;
}

.welcome-text {
    font-family: 'Exo', sans-serif;
    color: white;
    font-size: 5vw;
    font-weight:bold;
}

.welcome-line {
    position: relative;
    top: 0;
    height: 3px; 
    background-color: white;
    box-shadow: 5px 0px 5px rgba(0, 0, 0, 0.7);
}

.welcome-looped-text {
    font-size: 3vw;
    font-family: 'Inconsolata', monospace;
    letter-spacing:0.1em;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
}

.welcome-start {
    padding-top: 12%;
    color: black;
    font-weight: bold;
}

.welcome-center {
    margin: 0;
    position: absolute;
    color: white;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-shadow: 5px 1px 5px rgba(0, 0, 0, 0.7);
}

.welcome-btn, .welcome-btn:focus {
    position: absolute;
    top: 95%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
    background-color: transparent !important;
    transition-duration: 0.3s;
    outline: none;
    box-shadow: none;
    border: none;
    -webkit-tap-highlight-color: transparent;
}

.welcome-btn:hover, .welcome-btn:active {
    transition-duration: 0.3s;
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
    background-color: transparent !important;
    outline: none;
    box-shadow: none !important;
    border: none !important;
}

.welcome-svg {
    display: block;
    margin: 0 auto;
}

@media only screen and (min-width: 375px) {
    .welcome-line {
        top: -5px;
        height: 5px;
    }
}
#about {
    background: radial-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.8)),url(/personal_website/static/media/wood_bg.ee4759d6.jpg);
    background-size: cover;
    position:relative;
}

.about-title-row {
    padding-bottom: 5%;
}

.about-profile-row {
    padding: 5% 15% 0 15%;
}

.about-profile-pic {
    width: 100%;
    cursor: pointer;
    transition-duration: 0.5s;
}

.about-profile-pic:hover {
    transition-duration: 0.3s;
    box-shadow: 0 0 20px rgb(60, 50, 50);
}

.about-icon-effect {
    color:inherit;
    text-decoration: none;
    transition-duration: 0.6s;
}

.about-icon-effect:hover {
    color: rgb(255, 37, 37);
    transition-duration: 0.3s;
}

.about-info-box {
    position: relative;
    margin: 0 auto;
    padding: 2% 3% 4% 3%;
    width: 70%;
    height: 100%;
    border-radius: 5px;
    box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.5);
    background-color: rgba(220, 220, 220, 0.95);
}

.about-descriptions {
    color: black;
    text-align: center;
}

.about-icon {
    padding-top: 10%;
}

.about-resume {
    width: 100%;
    margin-top: 5%;
    margin-bottom: 5%;
}

.about-resume-btn, .about-resume-btn:focus{
    color: white;
    box-sizing: border-box;
    width: 100%;
    background-color: rgb(60, 50, 50);
    border-top: 2px solid rgba(0, 0, 0, 0.5);
    border-bottom: 2px solid rgba(0, 0, 0, 0.5);
    border-left-color: transparent;
    border-right-color: transparent;
    border-radius: 0%;
    transition-duration: 0.3s;
    box-shadow: 0 0 0 #000000 !important;
}

.about-resume-btn:hover {
    color: white;
    background-color: rgb(60, 50, 50) !important;
    border-top: 2px solid transparent !important;
    border-bottom: 2px solid transparent !important;
    border-left-color: transparent !important;
    border-right-color: transparent !important;
    outline: none !important;
    transition-duration: 0.3s;
    box-shadow: 0 0 10px rgb(255, 37, 37) !important;
}

.about-resume-svg {
    display: none;
}

.about-resume-btn:hover > .about-resume-text {
    color: transparent;
}

.about-resume-btn:hover > .about-resume-svg {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0 auto;
}

@media only screen and (min-width: 375px) {
    .about-resume-btn {
        padding: 1% 30%;
    }
}

@media only screen and (min-width: 768px) {
    .about-title-row {
        padding-bottom: 2%;
    }

    .about-profile-row {
        padding: 0;
    }

    .about-resume {
        margin-bottom: 0%;
    }

    .about-icon {
        padding-top: 20%;
    }

    .about-descriptions { 
        text-align: left;
    }
}
#education {
    background: radial-gradient(rgba(230, 230, 230, 0.95), rgba(230, 230, 230, 0.9),  rgba(245, 245, 245, 0.8), rgba(255, 255, 255, 0.9));
}

.education-uoftlogo {
    width: 100%;
    padding: 0 0;
}

.education-description {
    font-family: 'Inconsolata', monospace;
}

.education-info {
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-size: 0.8em;
}

.education-svg {
    margin: 0 auto;
}

.education-container {
    color: black;
    width: 70%;
}

.education-course {
    margin: 5% 0 1% 0;
}

.education-course-title {
    display: none;
}

.blob-btn, .blob-btn:focus {
    color: rgb(0, 45, 109);
    padding: 0.5% 3%;
    margin: 0.5% 0.7%;
    background-color: rgba(245, 245, 245, 0.6);
    border-color: rgb(0, 45, 109);
    border-radius: 25px;
    transition-duration: 0.3s;
    box-shadow: inset 0 0 0 black !important;
    display: flex;
}

.blob-btn:hover {
    color: white;
    background-color: rgb(0, 32, 78);
    border-color: rgb(0, 32, 78);
    border-radius: 25px;
    box-shadow: 0 0 10px rgb(0, 32, 78, 0.6) !important;
}

@media only screen and (min-width: 600px) {
    .education-uoftlogo {
        padding: 0 20%;
    }

    .education-info {
        font-size: 1em;
    }

    .education-course-title {
        display: inline-block;
    }
}

@media only screen and (min-width: 992px) {
    .education-uoftlogo {
        padding: 0 0 0 17%;
    }

    .education-svg {
        text-align: right;
        margin: 0 0 0 0;
    }
}

@media only screen and (min-width: 1200px) {
    .education-uoftlogo {
        padding: 0 0 0 17%;
    }

    .education-info {
        font-size: 1.2em;
    }

    .education-svg {
        text-align: right;
        margin: 0 0 0 10%;
    }
}

#experience {
    background-color: whitesmoke;
}

.experience-container {
    width: 70%;
}

.experience-logo {
    width: 100%;
}

.experience-content {
    padding-top: 3%;
}

.experience-loc-time {
    margin: 1% 0 1.2em 0;
}

.experience-description {
    text-align: left;
}

@media only screen and (min-width: 768px) {
    .experience-title {
        font-size: 1.5vw;
    }
}

@media only screen and (min-width: 992px) {
    .experience-logo {
        padding: 0 !important;
    }
}
#project {
    width: 100%;
    background: radial-gradient(rgba(230, 230, 230, 0.95), rgba(230, 230, 230, 0.9),  rgba(245, 245, 245, 0.8), rgba(255, 255, 255, 0.9));
}

.project-container {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
}

.project-title {
    margin: 0 auto;
    width: 70%;
}

.project-end {
    width: 70%;
    margin: 0 auto;
}

.project-carousel {
    position: relative;
    color: white;
    width: 100%;
    height: 50vh;
}

/* Modal Section */
.modal-carousel-pointer{
    color: gray;
    z-index: 1000;
}

.modal-divider {
    height: 0.05em;
    width: 95%;
    background-color: white;
    margin: 0 auto 1em auto;
}

.project-modal .modal-content {
    background-color: rgba(0, 0, 0, 0.75);
    color: white;
    border: 1px solid white;
    box-shadow: inset 0 0 8px white;
    padding: 1% 2% 1% 2%;
}

.modal-header {
    border-bottom: 0px;
}

/* Need this so background image does not jump */
.modal-open{
    padding:0 !important;
    overflow-y: inherit !important;
}

.modal-date-link {
    margin: 1% 0 1% 0;
}

.close {
    color: whitesmoke;
    margin: -1rem 0 -1rem auto !important;
}

.close:hover {
    color: white;
}
/* Modal Section */

.carousel-overlay {
    transition-duration: .5s;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
}

.carousel-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: 'Montserrat', sans-serif !important;
    font-weight: 700;
    font-style: italic;
    color: rgba(255, 255, 255, 0.8);
}

.project-item:hover .carousel-overlay {
    opacity: 1;
}

.carousel-pointer {
    color: gray;
}

.big-carousel-paging{
    margin: 0 auto 2% auto;
    color: black;
    font-style: italic;
}

.project-computer-carousel {
    height: 100%;
    width: 100%;
}

.project-mobile-carousel {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.project-item {
    position: absolute;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.project-item-image {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.overlay-main {
    font-size: 3em;
    transition-duration: 0.7s;
}

.overlay-left, .overlay-right {
    font-size: 1.45em;
    transition-duration: .5s;
}

.project-main:hover {
    cursor: pointer;
    opacity: 1 !important;
}

.project-left:hover {
    cursor: pointer;
    opacity: 0.8 !important;
}

.project-right:hover {
    cursor: pointer;
    opacity: 0.8 !important;
}

/* Small Carousel */
.sm-carousel-item:hover .sm-carousel-overlay {
    opacity: 1;
}

.sm-carousel-image {
    cursor: pointer;
}

.sm-carousel-overlay {
    opacity: 0;
    cursor: pointer;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 10;
}

.sm-carousel-text {
    font-family: 'Montserrat', sans-serif !important;
    font-weight: 700;
    font-style: italic;
    font-size: 3em;
    color: rgba(255, 255, 255, 0.8);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.carousel-control-, .carousel-control-next {
    z-index: 1000;
}

@media only screen and (min-width: 375px) {
    .project-carousel {
        height: 70vh;
    }
}

@media only screen and (min-width: 768px) {
    .project-carousel {
        height: 60vh;
    }
}

@media only screen and (min-width: 992px) {
    .project-carousel {
        height: 70vh;
    }
}

#skill {
    background-color: whitesmoke;
}

.skill-blob {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: black;
    padding: 3px 20px;
    margin: 5px 5px;
    border: 1px solid rgb(255, 37, 37);
    border-radius: 25px;
    transition-duration: 0.3s;
    box-shadow: 0 5px 5px rgb(255, 37, 37) !important;
    transition-duration: 0.5s;
}

.skill-svg {
    display: none;
}

.skill-blob:hover {
    transition-duration: 0.5s;
    transform: translateY(5px);
    box-shadow: 0 0 3px rgb(255, 37, 37) !important;
}

.skill-blob-row {
    margin-top: 3%;
}

.skill-title-bar {
    background-color: rgb(255, 37, 37);
    height: 1px;
    margin: 1px auto 3px auto;
    width: 20%;
}

.skill-first-row-2, .skill-bottom-row {
    margin-top: 5%;
}

@media only screen and (min-width: 768px) {
    .skill-first-row-2 {
        margin-top: 0;
    }

    .skill-title-bar {
        height: 0.07em;
    }
}
#contact {
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.95), rgba(0, 0, 0, 1));
    position:relative;
    height:100%;
    overflow: hidden;
    justify-content: center;
    padding-bottom: 0 !important;
}

.contact-container {
    position: relative;
    height: 100%;
    color: white;
    margin: 8% auto 0 auto;
    font-family: 'Montserrat', sans-serif;
}

.contact-sm-text {
    font-size: 0.9em;
}

.contact-md-text {
    font-size: 1em;
}

.contact-method {
    font-size: 1.2em;
}

.contact-row {
    position: relative;
}

.contact-icon {
    padding-top: 10%;
}

.contact-or-col {
    margin: 10% auto;
    padding: 0;
}

.contact-horizontal-line {
    height: 0.5px;
    position: relative;
    top: 0.8em;
    background-color: white;
}

.contact-form-row {
    margin-top: 3%;
    align-items: center;
}

.contact-icon-row {
    padding-bottom: 5%;
    margin-bottom: 2%;
    border-bottom: 1.5px solid white;
}

.copy-right-row1 {
    width: 100%;
    margin: 5% auto 0.5em auto;
    padding-top: 1em;
    border-top: 1.5px solid white;
}

.copy-right-row2 {
    margin: 0.5em auto 1em auto;
}

.contact-msg-category {
    float: left;
    padding-right: 0;
}

.contact-msg-textArea {
    max-height: 150px;
    min-height: 100px;
}

.contact-send-btn, .contact-send-btn:focus{
    box-sizing: border-box;
    width: 100%;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    background-color: black;
    border-color: rgba(255, 255, 255, 0.5);
    transition-duration: 0.5s;
    outline: none !important;
    box-shadow: 0 0 0 black !important;
}

.contact-send-btn:hover, .contact-send-btn:active {
    background-color: black !important;
    border-color: red !important;
    transition-duration: 0.3s;
    outline: none !important;
    box-shadow: 0 0 10px #ff2525 !important;
}

.contact-send-svg {
    display: none;
}

.contact-send-btn:hover > .contact-send-svg {
    display: block;
    font-size: 1.5em;
    margin: 0 auto;
}

.contact-send-btn:hover > .contact-send-text {
    display: none;  
}

.email-sent-status {
    margin: 0 auto 3% auto;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    border-radius: 5px;
}

.email-success {
    background-color: rgba(0, 128, 0, 0.4);
    border: 1.5px solid green;
}

.email-error {
    background-color: rgba(255, 0, 0, 0.4);
    border: 1.5px solid red;
}

input, textarea{
    outline: none !important;
    border:1px solid transparent !important;
    box-shadow: 0 0 0 black !important;
    transition-duration: 0.5s;
}

input:focus, textarea:focus {
    outline: none !important;
    border:1px solid red !important;
    box-shadow: 0 0 10px #ff2525 !important;
    transition-duration: 0.2s;
}

@media only screen and (min-width: 992px) {
    .contact-msg-category {
        float: right;
        padding-right: 5%;
    }

    .copy-right-row1 {
        width: 60%;
        margin-top: 2%;
    }

    .contact-or-col {
        margin: auto auto;
    }

    .contact-or {
        display: inline-block;
        margin-top: 50%;
        padding: 0 3%;
    }    

    .contact-form-row {
        align-items: flex-start;
    }

    .email-sent-status {
        margin: 0 auto 0 auto;
    }
}

